
html,
body {
	/*position: relative;*/
	height: 100%;
	/*background:#FFFDEF;*/
}
ul {
	list-style: none;
}
.header {
	width: 100%;
	height: 40px;
	line-height: 40px;
	background: #f4f4f4;
	color: #7bcca8;
	font-size: 16px;
	position: fixed;
	top: 0;
	left: 0;
	text-align: center;
	margin-bottom: 40px;
	z-index: 2;
}
.header img {
	height: 100%;
}
.content {
	width: 100%;
	height: 100%;
	margin-bottom: 50px;
	margin-top: 40px;
	/*position: fixed;
	left: 0;
	top: 40px;*/
}
.iconfont{
	font-size: 25px;
}
.footer {
	width: 100%;
	height: 50px;
	margin-top: 30px;
	position: fixed;
	bottom: 0;
	left: 0;
	background: #f4f4f4;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	z-index: 2;
}
.footer .iconfont {
	line-height: 50px;
}
#app {
	color: #2c3e50;
	/*margin-top: -100px;*/
	/*max-width: 600px;*/
	font-family: Source Sans Pro, Helvetica, sans-serif;
	height: 100%;
	position: relative;
}
#app a {
	color: #42b983;
	text-decoration: none;
}



